.allemployee{
  padding-top:2%;
  height:92vh;
  overflow-y:scroll;
  -ms-overflow-style: none; 
  scrollbar-width: none; 
}
.allemployee .card{
  display:flex;
  align-itdems:center;
  justify-content:center;
  width:100%;
  border:1px solid rgb(0, 137, 123);
  border-radius:20px;
  padding:8px;
  overflow:hidden;
  color:#26A69A;
  height:100%;
  /* box-shadow:1px 0px 3px 2px ; */
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  
}
.allemployee .card:hover{
  transform:scale(1.054);
  transition:all 0.7s ease;
}
